<template>
  <b-container>
    <h1> Drugs </h1>
    <div>
        <b-table striped hover :items="drugs"></b-table>
    </div>
  </b-container>
</template>

<script>
  export default {
    name: "DrugOverview",
    data: function() {
      return {
        drugs: [],
      }
    },
    methods: {
        getDrugs : function(){
            this.$http.get('https://mrs-isa-usijani.herokuapp.com/drugs')
            .then(response => {
		console.log(response);
                this.drugs = response.data.map(drug => 
                (
                    {
                        name: drug.name,
                        form: drug.form, 
                        type: drug.type.name,
                        receipt: drug.receipt ? "Yes" : "No",
                        manufacturer: drug.manufacturer.name,
                        substitutions: 
                            (!drug.substitutions || drug.substitutions.length == 0) 
                            ? "No substitute"
                            : drug.substitutions.map(sub => sub.name).join(", "),
                        ingredients: drug.ingredients.map(ingredient => ingredient.name).join(", "),
                    }
                ));
            })
            .catch(error => console.log(error));
        }
    },
    mounted: function(){
        this.getDrugs();
    }
  }
</script>
